.Profile {
  display: block;
  flex-direction: column;
  /* align-items: center; */
  /* justify-content: center; */
  /* width: 100vw; */
}

.page {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.5s ease;
  position: absolute; /* 場合によってはレイアウト崩れを防ぐため */
}

.page.active {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  position: relative;
}

.border_item {
  border: 1px solid white;
  width: 50vw;
  margin: 0 25vw;
}

.member_profile {
  min-width: 300px;
}